/**
 * Load fonts and assign variables
 */

@import url("//fonts.googleapis.com/css?family=Open+Sans:400,600,600italic");

$mdi-font-path: "../fonts";

$font-text: 400 100% "Open Sans";
$font-text-bold: 600 100% "Open Sans", Helvetica, Arial sans-serif;
$font-headline: 600 100% "Open Sans", Helvetica, Arial sans-serif;
$font-headline-italic: italic 600 100% "Open Sans", Helvetica, Arial sans-serif;
$font-icons: 100% "Material Design Icons";

/**
 * Default Page Elements
 */

body {
  color: $color-primary-text;
  font: $font-text;

  @include font-size(15px,24px);
}

strong,
b {
  font: $font-text-bold;
}

p {
  margin-bottom: 1em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: $font-headline;
}

h1 {
  @include font-size(30px,48px);
  margin: 2rem 0;
}

h2 {
  @include font-size(25px,36px);
  margin: 1.5rem 0;
}

h3 {
  font: $font-headline-italic;

  @include font-size(21px,28px);
  margin: 1.2rem 0;
}

h4 {
  @include font-size(17px,24px);
}

a {
  color: $color-primary;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.5s ease-in-out;

  &:hover,
  &:active {
    border-color: $color-primary-text;
    color: $color-primary-text;
  }
}

img {
  max-width: 100%;
}
